class ChubMLMod extends CML_Static {
  static ChubML = ChubMLMod
  ChubML = ChubMLMod

  // @ Options
  static {
    chaosGl.chubstart = () => { }
    window.onload = () =>
      chaosGl.chubstart?.()
  }

  s = CML_Static

  styled = {}

  #Rexps = () => this.s.Rexps
  #initialFormat(str: string) {
    let r = this.#Rexps
    return str
      .replace(r().lineWithComment, "")
      .replace(r().formatspace2, "\n")
      .replace(r().formatspace1, "\n")
  }

  #cascadeIndentList(str: string) {
    let strLines = str.split(/;/);

    let col = [] as CILEList

    for (const line of strLines) col.push({
      c: line.trim(),
      i: line.search(/\S/)
    });

    return col;
  }

  #sortCILIndent(contents: CILEList): SortedCILE | undefined {
    const sortedContents = [];
    let parentStack = [];

    for (const content of contents) {
      const currentIndent = content.i;
      const currentContent = { c: content.c, i: currentIndent, children: [] };

      while (parentStack.length > 0 && currentIndent <= parentStack[parentStack.length - 1].i)
        parentStack.pop();

      if (parentStack.length > 0) {
        const parent = parentStack[parentStack.length - 1];
        parent.children.push(currentContent);
      } else {
        sortedContents.push(currentContent);
      }

      parentStack.push(Object.assign(currentContent));
    };

    return sortedContents.shift();
  }

  #parseCIL(cil: CILEList, v = '') {

    let [parsedCILArr, indexes] = this.#traverse(cil, 0, v);

    // return this.#compile(parsedCILArr);
  }

  #makeDef = () => ({
    tag: "",
    id: "",
    class: "",
    content: "",
    data: "",
    attr: "",
    indent: 0,
  })

  #traverse(cil: CILEList, i: number, v = '') {
    let cilCopy = cil.slice();
    let indentString = "  "
    let indexes = {
      str: 0,
      tmp: 0,
    }

    let r = this.#Rexps
    let cnt = cil.c

    let def = this.#makeDef()
    let tempDef = this.#makeDef()

    // let isStr = cnt.search(reg.betweenQuote)
    // let hasOpts = strBuild.search(reg.betweenCol)
    // let isscr = strBuild.match(reg.script)

    return [cilCopy, indexes]
  }

  parse(source: string) {
    let str = this.#initialFormat(source)

    let indList = this.#cascadeIndentList(str)

    this.#parseCIL(indList)
  }
}

// @ Exporter
try { module.exports = new ChubMLMod } catch { }
